.snap-carousel {
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    scrollbar-width: none;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    list-style: none;
}

.snap-carousel::-webkit-scrollbar {
    display: none;
}

.snap-slide {
    outline: none;
    scroll-snap-align: start;
}