@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    @apply font-sans bg-blue-50 dark:bg-gray-900 text-gray-900 dark:text-blue-50;
}

.cta {
    @apply fill-current opacity-50 transition duration-200;
}

.cta:hover {
    @apply opacity-100;
}

button:focus {
    outline: thin dotted;
}

@import "./components/Snap/Snap.css";